.timeline {
  display: grid;
  grid-template-columns: 0.25rem 1fr;
  grid-auto-columns: max-content;
  column-gap: 2rem;
  list-style: none;
}
.timeline::before {
  content: "";
  grid-column: 1;
  grid-row: 1/span 20;
  background: var(--tb-light);
}
.timeline li {
  grid-column: 2;
  margin-inline: 1.5rem;
  grid-row: span 2;
  display: grid;
  grid-template-rows: min-content min-content min-content;
}
.timeline li:not(:last-child) {
  margin-bottom: 2rem;
}
.timeline li .date {
  height: 3rem;
  margin-inline: -1.5rem;
  text-align: center;
  color: #fff;
  display: grid;
  place-content: center;
  position: relative;
  border-radius: 1.5rem 0 0 1.5rem;
}
.timeline li .date::before {
  content: "";
  width: 1.8rem;
  aspect-ratio: 1;
  background: var(--tb-light);
  position: absolute;
  top: 100%;
  clip-path: polygon(0 0, 100% 0, 0 100%);
  right: 0;
}
.timeline li .date::after {
  content: "";
  display: block;
  position: absolute;
  width: 14px;
  height: 14px;
  background: #663b6c;
  border-radius: 15px;
  z-index: 1;
  top: 50%;
  transform: translate(50%, -50%);
  right: calc(100% + 2rem + 0.125rem);
}

.timeline li .title {
  position: relative;
  padding-inline: 1.5rem;
  overflow: hidden;
  padding-block-start: 1.5rem;
  padding-block-end: 1rem;
  font-weight: 500;
}
.timeline li .title::before {
  bottom: calc(100% + 0.125rem);
}
.timeline li .descr {
  position: relative;
  padding-inline: 1.5rem;
  padding-block-end: 1.5rem;
  font-weight: 300;
}
.timeline li .descr::before {
  z-index: -1;
  bottom: 0.25rem;
}

@media (min-width: 40rem) {
  .timeline {
    grid-template-columns: 1fr 0.25rem 1fr;
  }
  .timeline::before {
    grid-column: 2;
  }
  .timeline li:nth-child(odd) {
    grid-column: 1;
  }
  .timeline li:nth-child(odd) .date {
    border-radius: 0 1.5rem 1.5rem 0;
  }
  .timeline li:nth-child(odd) .date::before {
    clip-path: polygon(0 0, 100% 0, 100% 100%);
    left: 0;
  }
  .timeline li:nth-child(odd) .date::after {
    transform: translate(-50%, -50%);
    left: calc(100% + 2rem + 0.125rem);
  }
  .timeline li:nth-child(even) {
    grid-column: 3;
  }
  .timeline li:nth-child(2) {
    grid-row: 2/4;
  }
}
.timeline .timeline-date {
  width: 54px;
  height: 65px;
  display: inline-block;
  padding: 8px;
  clip-path: polygon(0 0, 100% 0, 100% 80%, 50% 100%, 0 80%);
  z-index: 1;
